'use client';

import React from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { motion, useReducedMotion, type Variants } from 'framer-motion';
import { MapPin, Clock, Phone, ArrowUpRight } from 'lucide-react';

import { Container } from '../container';
import { Button } from '../elements/button';

type ButtonTarget = '_self' | '_blank' | '_parent' | '_top' | string;

type CTAButton = {
  id?: number;
  text?: string | null;
  URL?: string | null;
  target?: ButtonTarget | null;
  variant?: string | null;
};

type Props = {
  heading: string;
  sub_heading?: string | null;

  location?: string | null;
  address?: string | null;
  maps_url?: string | null;
  opening_hours?: string | null;
  phone?: string | null;
  phone_note?: string | null;

  CTAs?: CTAButton[] | null;
  cover?: any;
  locale: string;
};

const STRAPI_BASE = (process.env.NEXT_PUBLIC_STRAPI_URL ?? '').replace(/\/$/, '');

function toAbsUrl(url?: string | null) {
  if (!url) return null;
  if (/^https?:\/\//i.test(url)) return url;
  if (!STRAPI_BASE) return url;
  return `${STRAPI_BASE}${url.startsWith('/') ? '' : '/'}${url}`;
}

function getMediaUrl(media: any): string | null {
  if (!media) return null;

  // Strapi v5 direct
  if (typeof media?.url === 'string' && media.url.length) return toAbsUrl(media.url);

  // v5 direct formats
  const directFormats = media?.formats;
  const directCandidates = [
    directFormats?.medium?.url,
    directFormats?.small?.url,
    directFormats?.thumbnail?.url,
    directFormats?.large?.url,
  ].filter(Boolean);
  if (directCandidates.length) return toAbsUrl(directCandidates[0]);

  // v4-ish
  const attr = media?.data?.attributes;
  if (typeof attr?.url === 'string' && attr.url.length) return toAbsUrl(attr.url);

  const fmts = attr?.formats;
  const candidates = [
    fmts?.medium?.url,
    fmts?.small?.url,
    fmts?.thumbnail?.url,
    fmts?.large?.url,
  ].filter(Boolean);
  if (candidates.length) return toAbsUrl(candidates[0]);

  return null;
}

function joinLocaleHref(locale: string, url?: string | null) {
  const u = (url ?? '').trim();
  const path = u ? (u.startsWith('/') ? u : `/${u}`) : '/';
  return `/${locale}${path}`;
}

function toTel(phone?: string | null) {
  if (!phone) return null;
  const cleaned = phone.replace(/[^\d+]/g, '');
  return cleaned ? `tel:${cleaned}` : null;
}

function mapSearchUrl(address?: string | null, location?: string | null) {
  const q = (address || location || '').trim();
  if (!q) return null;
  return `https://www.google.com/maps/search/?api=1&query=${encodeURIComponent(q)}`;
}

/* ──────────────────────────────────────────────
   Motion
────────────────────────────────────────────── */

const wrapV: Variants = {
  hidden: { opacity: 0, y: 14 },
  show: { opacity: 1, y: 0, transition: { duration: 0.55, ease: [0.22, 1, 0.36, 1] } },
};

const leftV: Variants = {
  hidden: {},
  show: { transition: { staggerChildren: 0.08, delayChildren: 0.04 } },
};

const itemV: Variants = {
  hidden: { opacity: 0, y: 10 },
  show: { opacity: 1, y: 0, transition: { duration: 0.42, ease: [0.22, 1, 0.36, 1] } },
};

export const CTA = ({
  heading,
  sub_heading,
  location,
  address,
  maps_url,
  opening_hours,
  phone,
  phone_note,
  CTAs,
  cover,
  locale,
}: Props) => {
  const reduce = useReducedMotion();

  const coverUrl = getMediaUrl(cover);
  const telHref = toTel(phone);
  const mapsHref = maps_url || mapSearchUrl(address, location);

  const contactText = (address || location || '').trim();
  const hasContactBar = !!(contactText || opening_hours || phone);

  const primaryCta = CTAs?.[0];
  const secondaryCta = CTAs?.[1];
  const extraCtas = (CTAs ?? []).slice(2);

  return (
    <motion.section
      variants={wrapV}
      initial={reduce ? false : 'hidden'}
      whileInView={reduce ? undefined : 'show'}
      viewport={{ once: true, amount: 0.35 }}
      className="relative mx-4 overflow-hidden rounded-3xl border border-charcoal bg-white shadow-sm"
    >
      {/* subtle premium background (nem túl sok) */}
      <div className="pointer-events-none absolute inset-0">
        <div className="absolute inset-0 bg-gradient-to-tr from-charcoal/35 via-white to-white" />
        <div className="absolute inset-0 opacity-[0.22] [background-image:linear-gradient(to_right,rgba(41,38,36,0.08)_1px,transparent_1px),linear-gradient(to_bottom,rgba(41,38,36,0.08)_1px,transparent_1px)] [background-size:44px_44px]" />
        <div className="absolute -bottom-28 -right-28 h-72 w-72 rounded-full bg-muted/10 blur-2xl" />
      </div>

      <Container className="relative z-10 py-10 md:py-14">
        <motion.div
          whileHover={reduce ? undefined : { y: -2 }}
          transition={{ duration: 0.22, ease: [0.22, 1, 0.36, 1] }}
          className="grid grid-cols-1 md:grid-cols-12 gap-9 md:gap-12 items-start"
        >
          {/* LEFT */}
          <motion.div
            variants={leftV}
            initial={reduce ? false : 'hidden'}
            whileInView={reduce ? undefined : 'show'}
            viewport={{ once: true, amount: 0.5 }}
            className="md:col-span-7"
          >
            <motion.h2
              variants={itemV}
              className="text-lightblack text-xl md:text-4xl font-bold leading-tight text-center md:text-left max-w-2xl mx-auto md:mx-0"
            >
              {heading}
            </motion.h2>

            {sub_heading ? (
              <motion.p
                variants={itemV}
                className="mt-5 md:mt-6 text-secondary text-sm md:text-base leading-relaxed text-center md:text-left max-w-xl mx-auto md:mx-0"
              >
                {sub_heading}
              </motion.p>
            ) : null}

            {/* CTA row (max 2 primary/secondary) */}
            {(primaryCta || secondaryCta) ? (
              <motion.div
                variants={itemV}
                className="mt-7 md:mt-8 flex flex-col sm:flex-row gap-3 items-stretch sm:items-center justify-center md:justify-start"
              >
                {primaryCta ? (
                  <Button
                    as={Link}
                    href={joinLocaleHref(locale, primaryCta.URL)}
                    variant={primaryCta.variant as any}
                    target={(primaryCta.target ?? undefined) as any}
                    rel={primaryCta.target === '_blank' ? ('noreferrer noopener' as any) : undefined}
                    className="group relative overflow-hidden w-full sm:w-auto rounded-xl bg-muted text-white border-none py-3 px-5"
                  >
                    <span className="relative z-10">{primaryCta.text}</span>
                    <span className="pointer-events-none absolute inset-0 opacity-0 group-hover:opacity-100 transition-opacity duration-300 bg-gradient-to-r from-white/0 via-white/25 to-white/0" />
                  </Button>
                ) : null}

                {secondaryCta ? (
                  <Button
                    as={Link}
                    href={joinLocaleHref(locale, secondaryCta.URL)}
                    variant={secondaryCta.variant as any}
                    target={(secondaryCta.target ?? undefined) as any}
                    rel={secondaryCta.target === '_blank' ? ('noreferrer noopener' as any) : undefined}
                    className="w-full sm:w-auto rounded-xl bg-white text-lightblack border border-charcoal hover:border-secondary/40 py-3 px-5"
                  >
                    {secondaryCta.text}
                  </Button>
                ) : null}
              </motion.div>
            ) : null}

            {/* Extra CTAs (if any) — keep minimal */}
            {extraCtas.length ? (
              <motion.div variants={itemV} className="mt-3 flex flex-col sm:flex-row gap-2 justify-center md:justify-start">
                {extraCtas.map((cta, i) => (
                  <Button
                    key={cta.id ?? i}
                    as={Link}
                    href={joinLocaleHref(locale, cta.URL)}
                    variant={cta.variant as any}
                    className="w-full sm:w-auto rounded-xl bg-white text-secondary border border-charcoal/70 hover:border-secondary/40 py-2.5 px-4"
                  >
                    {cta.text}
                  </Button>
                ))}
              </motion.div>
            ) : null}

            {/* INLINE CONTACT BAR (chips) */}
            {hasContactBar ? (
              <motion.div variants={itemV} className="mt-7 md:mt-8">
                <div className="flex flex-wrap items-center justify-center md:justify-start gap-2">
                  {/* Location chip (clickable if mapsHref) */}
                  {(contactText || mapsHref) ? (
                    <motion.div whileHover={reduce ? undefined : { y: -1 }} transition={{ duration: 0.18 }}>
                      {mapsHref ? (
                        <a
                          href={mapsHref}
                          target="_blank"
                          rel="noreferrer noopener"
                          className="group inline-flex items-center gap-2 rounded-full border border-charcoal bg-white/85 px-3 py-2 text-sm text-secondary shadow-sm hover:border-secondary/40"
                        >
                          <span className="inline-flex h-7 w-7 items-center justify-center rounded-full bg-charcoal/70 text-lightblack">
                            <MapPin className="h-4 w-4" />
                          </span>
                          <span className="max-w-[22rem] truncate">{contactText || 'Térkép'}</span>
                          <ArrowUpRight className="h-4 w-4 text-muted" />
                        </a>
                      ) : (
                        <div className="inline-flex items-center gap-2 rounded-full border border-charcoal bg-white/85 px-3 py-2 text-sm text-secondary shadow-sm">
                          <span className="inline-flex h-7 w-7 items-center justify-center rounded-full bg-charcoal/70 text-lightblack">
                            <MapPin className="h-4 w-4" />
                          </span>
                          <span className="max-w-[22rem] truncate">{contactText}</span>
                        </div>
                      )}
                    </motion.div>
                  ) : null}

                  {/* Hours chip */}
                  {opening_hours ? (
                    <motion.div whileHover={reduce ? undefined : { y: -1 }} transition={{ duration: 0.18 }}>
                      <div className="inline-flex items-center gap-2 rounded-full border border-charcoal bg-white/85 px-3 py-2 text-sm text-secondary shadow-sm hover:border-secondary/40">
                        <span className="inline-flex h-7 w-7 items-center justify-center rounded-full bg-charcoal/70 text-lightblack">
                          <Clock className="h-4 w-4" />
                        </span>
                        <span className="truncate">{opening_hours}</span>
                      </div>
                    </motion.div>
                  ) : null}

                  {/* Phone chip */}
                  {phone ? (
                    <motion.div whileHover={reduce ? undefined : { y: -1 }} transition={{ duration: 0.18 }}>
                      {telHref ? (
                        <a
                          href={telHref}
                          className="inline-flex items-center gap-2 rounded-full border border-charcoal bg-white/85 px-3 py-2 text-sm text-secondary shadow-sm hover:border-secondary/40 hover:text-lightblack transition-colors"
                        >
                          <span className="inline-flex h-7 w-7 items-center justify-center rounded-full bg-charcoal/70 text-lightblack">
                            <Phone className="h-4 w-4" />
                          </span>
                          <span className="truncate">{phone}</span>
                        </a>
                      ) : (
                        <div className="inline-flex items-center gap-2 rounded-full border border-charcoal bg-white/85 px-3 py-2 text-sm text-secondary shadow-sm">
                          <span className="inline-flex h-7 w-7 items-center justify-center rounded-full bg-charcoal/70 text-lightblack">
                            <Phone className="h-4 w-4" />
                          </span>
                          <span className="truncate">{phone}</span>
                        </div>
                      )}
                    </motion.div>
                  ) : null}
                </div>

                {/* phone_note (Strapi content) – optional, no extra marketing copy */}
                {phone_note ? (
                  <div className="mt-3 text-center md:text-left text-sm text-secondary">
                    {phone_note}
                    <span className="text-muted font-semibold"> előbb beszéljük át.</span>
                  </div>
                ) : null}
              </motion.div>
            ) : null}
          </motion.div>

          {/* RIGHT (Cover) */}
          <motion.div
            initial={reduce ? false : { opacity: 0, y: 10, scale: 0.985 }}
            whileInView={reduce ? undefined : { opacity: 1, y: 0, scale: 1 }}
            viewport={{ once: true, amount: 0.35 }}
            transition={{ duration: 0.55, ease: [0.22, 1, 0.36, 1] }}
            whileHover={reduce ? undefined : { y: -3 }}
            className="md:col-span-5 w-full"
          >
            <div className="group relative w-full overflow-hidden rounded-3xl border border-charcoal bg-charcoal/40 shadow-sm">
              <div className="relative h-[240px] md:h-[360px] w-full">
                {coverUrl ? (
                  <Image
                    src={coverUrl}
                    alt={heading || 'CTA cover'}
                    fill
                    sizes="(min-width: 768px) 520px, 100vw"
                    className="object-cover"
                    priority={false}
                  />
                ) : (
                  <>
                    <div className="absolute inset-0 bg-gradient-to-tr from-charcoal via-white to-white" />
                    <div className="absolute inset-0 bg-gradient-to-tr from-lightblack/10 via-transparent to-muted/15" />
                  </>
                )}

                {/* vignette */}
                <div className="pointer-events-none absolute inset-0 bg-gradient-to-t from-lightblack/28 via-transparent to-transparent" />

                {/* sheen (hover) */}
                <motion.div
                  initial={reduce ? false : { x: -220, opacity: 0 }}
                  animate={reduce ? undefined : { x: -220, opacity: 0 }}
                  whileHover={
                    reduce
                      ? undefined
                      : { x: 220, opacity: 1, transition: { duration: 0.9, ease: [0.22, 1, 0.36, 1] } }
                  }
                  className="pointer-events-none absolute -inset-y-16 left-1/2 w-[220%] -translate-x-1/2 rotate-12 bg-gradient-to-r from-white/0 via-white/20 to-white/0 blur-[2px]"
                />

                {/* muted highlight */}
                <div className="pointer-events-none absolute inset-0 bg-gradient-to-tr from-transparent via-muted/10 to-transparent opacity-90 group-hover:opacity-100 transition-opacity duration-300" />
              </div>
            </div>
          </motion.div>
        </motion.div>
      </Container>
    </motion.section>
  );
};
